@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;500&display=swap);
html,body{margin:0;padding:0;width:100%;height:100%;font-family:"Roboto", sans-serif}#root{color:lightgray;height:100%;background-image:url(/static/media/bg.1f32eff1.png);background-position:center;background-repeat:no-repeat;background-size:cover}h1{margin-top:0;font-size:34px}p{margin-top:12px;margin-bottom:12px;font-size:24px;font-weight:100;text-align:center}.error{text-align:center;margin:0}.header{height:200px;padding-left:100px;box-sizing:border-box}.header .logo{height:200px}.main{height:calc(100% - 200px);display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:flex-start;justify-content:flex-start}.main .caption{color:white;font-weight:300}.link{color:white;text-decoration:underline;font-weight:500}

